//const BASE_URL = 'http://10.16.29.101:8080';
const BASE_URL = '';
const service = options => new Promise((resolve, reject) => {
  $.ajax({
    type: options.method,
    url: BASE_URL + options.url,
    data: options.data,
    dataType: 'json',
    contentType: "application/x-www-form-urlencoded; charset=utf-8", //防止中文乱码
    timeout:8000,
    success(data) {
      store.dispatch('setLoad',false)//停止加载
      resolve(data)
    },
    error(msg) {
      if (msg.status == 403) {
        store.dispatch('FedLogOut').then(() => {
          window.location.reload();
        }).catch(er => {
          console.log('登出失败');
        });
      }
      reject(msg);
    }
  })
})


export default service;



